"use client"

import { useEffect, useState } from "react"
import { Card } from "@/components/ui/card"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

interface TrackingData {
  trackingId: string
  timestamp: string
  geolocation?: {
    latitude: number
    longitude: number
  }
  ipAddress?: string
}

export default function DashboardPage() {
  const [trackingData, setTrackingData] = useState<TrackingData[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState("")

  useEffect(() => {
    const fetchTrackingData = async () => {
      try {
        const response = await fetch("/api/track")
        const data = await response.json()

        if (response.ok) {
          setTrackingData(data.data)
        } else {
          setError(data.error || "Failed to fetch tracking data")
        }
      } catch (err) {
        setError("An error occurred while fetching tracking data")
      } finally {
        setIsLoading(false)
      }
    }

    fetchTrackingData()
  }, [])

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString()
  }

  const formatLocation = (geolocation?: { latitude: number; longitude: number }) => {
    if (!geolocation) return "Not available"
    return `${geolocation.latitude.toFixed(6)}, ${geolocation.longitude.toFixed(6)}`
  }

  return (
    <div className="container mx-auto px-4 py-16">
      <Card className="p-8">
        <div className="space-y-6">
          <div className="space-y-2">
            <h1 className="text-3xl font-bold">Tracking Dashboard</h1>
            <p className="text-gray-500">View all link visits and their locations</p>
          </div>

          {isLoading ? (
            <div className="text-center py-8">
              <div className="animate-spin h-8 w-8 border-4 border-blue-500 rounded-full border-t-transparent mx-auto"></div>
              <p className="mt-4">Loading tracking data...</p>
            </div>
          ) : error ? (
            <div className="text-red-500 text-center py-8">{error}</div>
          ) : trackingData.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              No tracking data available yet
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Tracking ID</TableHead>
                    <TableHead>Timestamp</TableHead>
                    <TableHead>Location (Lat, Long)</TableHead>
                    <TableHead>IP Address</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {trackingData.map((record, index) => (
                    <TableRow key={index}>
                      <TableCell>{record.trackingId}</TableCell>
                      <TableCell>{formatDate(record.timestamp)}</TableCell>
                      <TableCell>{formatLocation(record.geolocation)}</TableCell>
                      <TableCell>{record.ipAddress || "Unknown"}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </div>
      </Card>
    </div>
  )
}
